@layer components {
	.variations {
		@apply relative mt-3 mb-0 lg:w-2/4;

		td,
		th {
			border: 0;
		}

		.label,
		.value {
			@apply block;
		}

		.label {
			@apply p-0;
			label {
				@apply text-sm text-typo-secondary mb-1;
			}
		}

		.value {
			@apply p-0 mb-3;
		}

		.reset_variations {
			@apply block text-sm text-typo-secondary mt-2 underline;
		}
	}

	.single_variation {
		.woocommerce-variation-description {
			@apply relative mt-3 mb-6;
			* {
				@apply text-sm text-typo-secondary;
			}
		}
		.woocommerce-variation-price {
		}

		.woocommerce-variation-availability {
			@apply relative my-3;
		}
	}

	.woocommerce-variation-price {
		@apply text-3xl;
		@apply mb-6;
	}

	.variation {
		@apply flex mt-1 mb-2;

		dd {
			@apply ml-1 mr-2 text-xs mb-0;
			* {
				@apply text-xs;
			}
		}

		dt {
			@apply text-xs;
		}
	}
}
